import { useEffect } from 'react';
import { Navigate, Outlet, useNavigate } from 'react-router-dom';

// material-ui
import useMediaQuery from '@mui/material/useMediaQuery';
import Toolbar from '@mui/material/Toolbar';
import Box from '@mui/material/Box';

// project import
import Drawer from './Drawer';
import Header from './Header';
import navigation from 'menu-items';
import Loader from 'components/Loader';
import Breadcrumbs from 'components/@extended/Breadcrumbs';

import { handlerDrawerOpen, useGetMenuMaster } from 'api/menu';
import { useDispatch, useSelector } from 'react-redux';
import { getApplicationData } from 'store/actions/app.actions';
import Footer from './Footer';

// ==============================|| MAIN LAYOUT ||============================== //

export default function DashboardLayout() {
  const { menuMasterLoading } = useGetMenuMaster();
  const downXL = useMediaQuery((theme) => theme.breakpoints.down('xl'));
  const navigate = useNavigate();

  const dispatch = useDispatch();

  const { isLoading, isAuthenticated } = useSelector((state) => state.app);

  useEffect(() => {
    handlerDrawerOpen(!downXL);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [downXL]);

  useEffect(() => {
    if (!isLoading && !isAuthenticated) {
      navigate('/login');
    }
  }, [isAuthenticated, isLoading, navigate]);

  useEffect(() => {
    if (isAuthenticated) {
      dispatch(getApplicationData());
    }
  }, [dispatch, isAuthenticated]);

  if (menuMasterLoading) return <Loader />;

  if (isLoading) {
    return <Loader />;
  }

  return (
    <Box sx={{ display: 'flex',flexDirection: 'column', width: '100%' }}>
      <Header />
      {/* <Drawer /> */}
      <Box component="main" sx={{ width: 'calc(100%)', px: '20px', pt: '50px', display: 'flex', flexDirection: 'column' }}>
        {/* <Toolbar /> */}
        {/* <Breadcrumbs navigation={navigation} title /> */}
        <Box height={'20px'} />
        <Outlet />
      </Box>
      <Footer />
    </Box>
  );
}
